/******************************************************************************
 * Content
 ******************************************************************************/

@use "variables" as v;

body {
 padding-bottom: 70px;
 color: var(--global-text-color);
 background-color: var(--global-bg-color);

 h1,
 h2,
 h3,
 h4,
 h5,
 h6 {
 scroll-margin-top: 66px;
 }
}

body.fixed-top-nav {
 // Add some padding for the nav-bar.
 padding-top: 57px;
}

body.sticky-bottom-footer {
 // Remove padding below footer.
 padding-bottom: 0;
}

.container {
 max-width: v.$max-content-width;
}

// Profile
.profile {
 img {
 width: 100%;
 }
}

// Content layout uses Bootstrap's grid system with responsive columns
// Main content flows naturally through container-mt-5 and Bootstrap grid classes
